<style lang="less">
    @import './login.less';
</style>
<template>
    <div class="login" >
        <div class="login-con">
            <Card :bordered="false">
                <p slot="title">
                    <Icon type="log-in"></Icon>
                    欢迎登录
                </p>
                <div class="form-con">
                    <Form ref="loginForm" :model="form" :rules="rules">
                        <FormItem prop="userName">
                            <Input v-model="form.userName" placeholder="请输入用户名">
                                <span slot="prepend">
                                    <Icon :size="16" type="person"></Icon>
                                </span>
                            </Input>
                        </FormItem>
                        <FormItem prop="password">
                            <Input v-model="form.password" type="password" placeholder="请输入密码">
                                <span slot="prepend">
                                    <Icon :size="14" type="locked"></Icon>
                                </span>
                            </Input>
                        </FormItem>
                        <Checkbox v-model="single" label="rem" @on-change="remember">记住密码</Checkbox>
                        <p v-if="seen" class="login-tip">{{errorMes}}</p>
                        <FormItem>
                            <Button  type="primary" @click="submit" long>登录</Button>
                        </FormItem>
                    </Form>

                </div>
            </Card>
        </div>
    </div>
</template>


<style>

</style>
<script>
import Cookies from 'js-cookie'
    export default {
        data () {
            return {
                errorMes:'',
                seen: false,
                disabled:true,
                single:true,
                form: {
                    userName: '',
                    password: ''
                },
                rules: {
                    userName: [
                        { required: true, message: '账号不能为空', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '密码不能为空', trigger: 'blur' }
                    ]
                },

            };
        },
        created(){
          this.form.userName  = Cookies.get('myUserName');
            this.form.password  = Cookies.get('mypassWord');
            console.log(this.form.userName)
            console.log(this.form.password)
            if (this.form.userName==undefined&&this.form.password==undefined) {
                console.log(this.form.userName)
                this.single = false;
                console.log(this.single)
            }else{
                this.single = true;
            }
        },

        methods:{
            submit(){
                const url = "/user/userLogin.json?userName="+this.form.userName+"&password="+this.form.password
                this.$Http.post(url,{}).then(res=>{
                    console.log(res)
                    console.log(res.body.data.token)
                    console.log(res.body.msg)
                    if(res.body.status == 0){
                        this.seen=false;
                        window.sessionStorage.setItem('token',res.body.data.token);
                        window.sessionStorage.setItem('userName',this.form.userName);
                        this.$router.push({path: '/homePage'});
                    }else if(res.body.status == 400){
                        this.seen=true;
                        this.errorMes=res.body.msg;
                    }
                })
            },
            remember (data) {
                console.log(data);
                console.log(this.single)
                if (this.single == true) {
                    Cookies.set('myUserName', this.form.userName,{expires:30});
                    Cookies.set('mypassWord', this.form.password,{expires:30});
                }else{
                    Cookies.remove('myUserName');
                    Cookies.remove('mypassWord');
                }
                const myUserName  = Cookies.get('myUserName');
                const mypassWord  = Cookies.get('mypassWord');
                console.log(myUserName+"＝＝   "+mypassWord)
            },

        }
    }


</script>




